import Link from 'next/link'

function LoggedInNavbar(userConfig) {
  return (
    <div>
      <Link href="/">
        <a>Dashboard</a>
      </Link>
      <Link href="/logout">
        <a>Logout</a>
      </Link>
    </div>
  );
}

function LoggedOutNavbar() {
  return (
    <div>
      <Link href="/login">
        <a>Login</a>
      </Link>
    </div>
  );
}

export default function Navbar({ userConfig }) {
  return userConfig ? LoggedInNavbar(userConfig) : LoggedOutNavbar();
}